import React from 'react'
import PropTypes from 'prop-types';


class Title extends React.Component {
  render() {
    return (
        <div className='ml3'>
        <h2 className='dib code fw4 pa2 tracked'>{this.props.text}</h2>
        </div>
    );
  }
}
Title.propTypes = {
  text: PropTypes.string.isRequired,
};


export default Title



