import React from 'react'
import PropTypes from 'prop-types';
import Link from 'gatsby-link'

class WorkTile extends React.Component {
  render() {
    return (
<div className='w-50-ns w-100 pa2 dib center'>
  <Link className='no-underline' to={this.props.link}>
    <div className='relative center'>
    <div className='w-100 ph3 pv3'>
    <h2 className='ma0 fw4 pa0'>{this.props.title}<span className='hint-text code fw5 f5 tracked'> &#8226; {this.props.info}</span></h2>
    <p style={{lineHeight: '24px'}} className='secondary-text mt2 mb0 fw4' >{this.props.summary} </p>
    </div>
  <div className='pa3'>
   <img className='hoverable center db'src={this.props.img} alt='UX/UI'/>
   </div>
  </div>
  </Link>
</div>

    )
  }
}
WorkTile.propTypes = {
  link: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  info: PropTypes.string.isRequired,
  summary: PropTypes.string.isRequired,
  img: PropTypes.string.isRequired,
  live: PropTypes.string.isRequired,
};
export default WorkTile




